import React, { useEffect, useState } from "react";
import { YMaps, withYMaps } from "react-yandex-maps";
import user from "../../assets/images/user2.png";
import car from "../../assets/images/car.png";
import market from "../../assets/images/market.png";

function roteteImageCar(heading) {
	const imageCar = document.querySelector(".ymaps-2-1-79-image ");
	if (imageCar) {
		imageCar.style.transform = `rotate(${heading}deg)`;
	}
}

const MyCustomRoute = withYMaps(({ ymaps, id, route, mapOptions, heading }) => {
	const [newMultiRouteModel, setMultiRouteModel] = useState();

	useEffect(() => {
		if (ymaps) {
			let multiRouteModel = new ymaps.multiRouter.MultiRouteModel(route, {
				avoidTrafficJams: true,
				results: 1
			});
			setMultiRouteModel(multiRouteModel);

			let multiRoute = new ymaps.multiRouter.MultiRoute(multiRouteModel, {
				wayPointStartIconLayout: "default#image",
				wayPointStartIconImageHref: car,
				wayPointStartIconImageOffset: [-17, -20],

				wayPointIconLayout: "default#image",
				wayPointIconImageHref: market,
				wayPointIconImageOffset: [-17, -40],
				wayPointIconImageRotation: heading,

				wayPointFinishIconLayout: "default#image",
				wayPointFinishIconImageHref: user,
				wayPointFinishIconImageOffset: [-17, -40],
				boundsAutoApply: true
			});

			let map = new ymaps.Map(id, {
				...mapOptions
			});
			map.geoObjects.add(multiRoute);
		}
		return () => {
			let e = document.querySelector("#custom-map #" + id);
			e.innerHTML = "";
		};
	}, [ymaps]);

	useEffect(() => {
		if (newMultiRouteModel) {
			newMultiRouteModel.setReferencePoints(route);
			roteteImageCar(heading ? heading : 0);
		}
	}, [route, heading]);

	return "";
});

const YandexMapWithRoute = ({ route, mapOptions, heading }) => {
	const [mapId] = useState("map" + Math.floor(Math.random() * 1000));
	return (
		<div id="custom-map">
			<YMaps
				query={{
					apikey: "dd5ae394-c34a-482d-a3a0-0c66d4560cc3",
					lang: "ru_RU",
					load:
						"route,Map,multiRouter.MultiRoute,multiRouter.MultiRouteModel"
				}}
			>
				<MyCustomRoute
					id={mapId}
					heading={heading}
					route={route}
					mapOptions={mapOptions}
				/>
			</YMaps>
			<div id={mapId}></div>
		</div>
	);
};

export default YandexMapWithRoute;
