import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setCategoryPath } from "../../actions/any";
import ProfileOne from "./ProfileOne";
import ProfileTwo from "./ProfileTwo";
import market from "../../config/market";
import ProfileThree from "./ProfileThree";
import { Redirect, useHistory } from "react-router-dom";

export default function ShopRoute() {
	const { token } = useSelector((state) => state.user.tokenData);
	const dispatch = useDispatch();
	const history = useHistory();
	useEffect(() => {
		dispatch(setCategoryPath(null));
	}, [dispatch]);

	if (!token) {
		if (window.webview) {
			window.sendMessageToApp("TOKEN_NOT_DEFINED");
			return <Redirect back />;
		}
		return (
			<Redirect
				to={{
					pathname: "/login",
					state: { ...(history.location.state || {}) },
				}}
			/>
		);
	}

	switch (market.config.profile) {
		case 1:
			return <ProfileOne />;
		case 2:
			return <ProfileTwo />;
		case 3:
			return <ProfileThree />;
		default:
			return <ProfileOne />;
	}
}
