import React, { Fragment, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Field, FieldArray, reduxForm } from "redux-form";
import validate from "./validate";

const otherField = props => {
	let {
		input,
		label,
		type,
		className,
		meta: { touched, error },
		checked
	} = props;

	if (touched) {
		if (error) {
			className = className + " warning";
		}
	}
	return (
		<Fragment>
			<input
				{...input}
				placeholder={label}
				className={className}
				type={type}
				checked={checked}
			/>
			{touched && error && <span>{error}</span>}
		</Fragment>
	);
};

function LegalForm({ handleSubmit }) {
	return (
		<form className="firmForm" onSubmit={handleSubmit}>
			<FieldArray name="firmProfile" component={firmProfile} />
		</form>
	);
}

const firmProfile = ({ fields }) => {
	const { t } = useTranslation();
	useEffect(() => {
		fields.push({
			firmName: null,
			legalName: null,
			inn: null,
			okonh: null,
			account: null,
			bank: null,
			mainAddress: null,
			legalAddress: null
		});
	}, []);

	return (
		<Fragment>
			{fields.map((firm, index) => (
				<Fragment key={index}>
					<div
						style={{
							width: "max-content",
							marginLeft: "auto",
							textAlign: "right",
							cursor: "pointer"
						}}
					>
						<span onClick={() => fields.remove(index)}>
							<i className="icon-close"></i>
						</span>
					</div>
					<div className="row">
						<div className="col-6 form-group">
							<label htmlFor={`${firm}.firmName`}>
								{t("firm-name")} *
							</label>
							<Field
								type="text"
								className="form-control"
								component={otherField}
								id={`${firm}.firmName`}
								name={`${firm}.firmName`}
								required
							/>
						</div>
						<div className="col-6 form-group">
							<label htmlFor={`${firm}.legalName`}>
								{t("legal-name")} *
							</label>
							<Field
								type="text"
								className="form-control"
								component={otherField}
								id={`${firm}.legalName`}
								name={`${firm}.legalName`}
								required
							/>
						</div>
					</div>
					<div className="row">
						<div className="col-6 form-group">
							<label htmlFor={`${firm}.inn`}>{t("inn")} *</label>
							<Field
								type="text"
								className="form-control"
								component={otherField}
								id={`${firm}.inn`}
								name={`${firm}.inn`}
								required
							/>
						</div>
						<div className="col-6 form-group">
							<label htmlFor={`${firm}.mfo`}>{t("mfo")} *</label>
							<Field
								type="text"
								className="form-control"
								component={otherField}
								id={`${firm}.mfo`}
								name={`${firm}.mfo`}
								required
							/>
						</div>
					</div>
					<div className="row">
						<div className="col-6 form-group">
							<label htmlFor={`${firm}.okonh`}>
								{t("okonx")} *
							</label>
							<Field
								type="text"
								className="form-control"
								component={otherField}
								id={`${firm}.okonh`}
								name={`${firm}.okonh`}
								required
							/>
						</div>
						<div className="col-6 form-group">
							<label htmlFor={`${firm}.account`}>
								{t("firm-account")} *
							</label>
							<Field
								type="text"
								className="form-control"
								component={otherField}
								id={`${firm}.account`}
								name={`${firm}.account`}
								required
							/>
						</div>
					</div>
					<div className="row">
						<div className="col-12 form-group">
							<label htmlFor={`${firm}.bank`}>
								{t("bank")} *
							</label>
							<Field
								type="text"
								className="form-control"
								component={otherField}
								id={`${firm}.bank`}
								name={`${firm}.bank`}
								required
							/>
						</div>
						<div className="col-12 form-group">
							<label htmlFor={`${firm}.mainAddress`}>
								{t("main-address")} *
							</label>
							<Field
								type="text"
								className="form-control"
								component={otherField}
								id={`${firm}.mainAddress`}
								name={`${firm}.mainAddress`}
								required
							/>
						</div>

						<div className="col-12 form-group">
							<label htmlFor={`${firm}.legalAddress`}>
								{t("legal-address")} *
							</label>
							<Field
								type="text"
								className="form-control"
								component={otherField}
								id={`${firm}.legalAddress`}
								name={`${firm}.legalAddress`}
								required
							/>
						</div>
					</div>
					<button type="submit" className="btn btn-primary mx-auto">
						{t("add")}
					</button>
				</Fragment>
			))}
			<div
				onClick={() =>
					fields.push({
						firmName: null,
						legalName: null,
						inn: null,
						okonh: null,
						account: null,
						bank: null,
						mainAddress: null,
						legalAddress: null
					})
				}
				style={{
					display: "flex",
					alignItems: "center",
					justifyContent: "center",
					borderRadius: "5px",
					padding: "10px 20px",
					margin: "0 auto",
					width: "max-content",
					border: "1px solid #eee",
					cursor: "pointer",
					color: "#666"
				}}
			>
				<div>
					<i className="icon-plus mr-3" color="#999"></i>
				</div>{" "}
				<span>{t("add-company")}</span>
			</div>
		</Fragment>
	);
};

export default reduxForm({
	form: "registrationForm",
	validate
})(LegalForm);
