import React from "react";
import Modal from "react-modal";
import { useDispatch, useSelector } from "react-redux";
import { closeModal } from "../../../actions/modal";
import { formatterCurrency } from "../../../utils/string";
import { calculateSellsDiscount } from "../../../views/checkout/helpers";
import SellRecordTwo from "../../../views/profile/dashboard/SellRecordTwo";
import car from "../../../assets/images/Vector.png";
import { useTranslation } from "react-i18next";

const customStyles = {
	content: {
		maxWidth: "100%",
		maxHeight: "max-content"
	},
	overlay: {
		backgroundColor: "rgba(77,77,77,0.6)",
		overflow: "auto",
		zIndex: "1041"
	}
};

function OrderDetailsModal({ prop }) {
	const dispatch = useDispatch();
	const { order, total, shippingService } = prop;
	// console.log(total);

	const { sells, shippingAddress } = order;
	const showModal = useSelector(state => state.modal.showModal);
	const modal = useSelector(state => state.modal.modal);
	const { paymentMethods } = useSelector(state => state.marketData.cashdesk);
	const { t } = useTranslation();
	const paidPaymentMethod = paymentMethods.find(
		p => p.id === sells[0]?.payments[0]?.paymentMethod
	);

	const sellsDiscount = calculateSellsDiscount(sells);

	function close() {
		setTimeout(() => {
			dispatch(closeModal("order-details"));
		}, 200);
	}

	return (
		<Modal
			isOpen={showModal && "order-details" === modal}
			onRequestClose={close}
			style={customStyles}
			className="modal-dialog"
			id="order-details"
		>
			<div className="modal-content">
				<div className="orders_header">
					<p
						style={{ cursor: "pointer" }}
						className="exit-product-page"
						onClick={() => close()}
					>
						<i className="icon-arrow-left"></i>
					</p>
					<h3>{t("order-details")}</h3>
				</div>
				<div className="order-content">
					<div className="info-order-item">
						<div className="info_order">
							<div className="info-order-content">
								<div className="delivery_type">
									<img
										src={car}
										alt="car"
										width="20"
										height="20"
									/>
									<p className="delivery_name mr-2">
										{shippingService?.name}
									</p>
									{shippingService?.price ? (
										shippingService?.price[
											shippingService?.price.first
										] > 0 ? (
											<p className="delivery_price">
												{formatterCurrency(
													shippingService?.price[
														shippingService?.price
															.first
													]
												)}
											</p>
										) : (
											<p className="delivery_price">
												{formatterCurrency(
													shippingService?.price[
														shippingService?.price
															.first
													]
												)}
											</p>
										)
									) : (
										""
									)}
								</div>
								{sells
									.filter(s => !s.deleted)
									.map((sell, index) => {
										return (
											<div
												className="sell_group"
												key={index}
											>
												<div className="info_sell">
													<p className="sell-id m-0">
														{t("order")} #{sell.id}
													</p>
												</div>
												<div className="sell_items">
													{sell.sellRecords
														.filter(s => !s.deleted)
														.map(
															(
																sellRecord,
																index
															) => {
																return (
																	<SellRecordTwo
																		key={
																			index
																		}
																		sellRecord={
																			sellRecord
																		}
																	/>
																);
															}
														)}
												</div>
											</div>
										);
									})}
							</div>
						</div>
						<div className="summary">
							<div className="table-wrapper">
								<div className="table table-summary">
									<div className="summary-subtotal">
										<p>{t("productstwo")}</p>
										<p>{formatterCurrency(total)}</p>
									</div>
									{sellsDiscount > 0 ? (
										<div className="summary-subtotal discount">
											<p>{t("discount")}</p>
											<p>
												{formatterCurrency(
													sellsDiscount
												)}
											</p>
										</div>
									) : null}
									{shippingService?.price ? (
										<div className="summary-shipping">
											<p>{t("delivery")}</p>
											<p>
												{formatterCurrency(
													shippingService.price[
														shippingService.price
															.first
													]
												)}
											</p>
										</div>
									) : (
										""
									)}
								</div>
								<div className="location">
									<p>{shippingAddress.addressLine1}</p>
								</div>
								<div className="payment">
									<p>{t("payment")}:</p>
									<p>
										<img
											src={`https://app.ox-sys.com${paidPaymentMethod?.icon}`}
											alt="card"
											width="25"
											height="20"
										/>
										<span>{paidPaymentMethod?.name}</span>
									</p>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</Modal>
	);
}

export default OrderDetailsModal;
