import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { connect, useDispatch, useSelector } from "react-redux";
import { logoutUser, setUserData } from "../../actions/user";
import { emptyCart } from "../../actions/cart";
import Purchases from "./purchases/Purchases";
import Settings from "./settings/Settings";
import Company from "./Company/Company";
import Addresses from "./addresses/addresses";
import { useTranslation } from "react-i18next";
import DashboardTwo from "./dashboard/DashboardTwo";
import { getUserByToken } from "../../api/user";
import LoaderOverlay from "../../components/features/loaders/LoaderOverlay";
import market from "../../config/market";
import RiveLoader from "../../components/features/loaders/RiveLoader";
import UserCards from "./userCards/UserCards";
import BackButton from "../../components/common/BackButton";

function Profile({ user, logoutUser, emptyCart, token }) {
	const tokenData = useSelector(state => state.user.tokenData);
	const { t } = useTranslation();
	const dispatch = useDispatch();
	const [activePanel, setActivePanel] = useState(1);

	useEffect(() => {
		if (token && !user.username) {
			getUserByToken(token).then(res => {
				dispatch(setUserData(res, tokenData));
			});
		}
	}, []);

	return !user.username ? (
		market.config.loader ? (
			<RiveLoader />
		) : (
			<LoaderOverlay />
		)
	) : (
		<>
			<div className="profile-three">
				<div className="page-content">
					<div className="dashboard">
						<div className="container header-container d-none">
							<div className="dashboard_header">
								<BackButton
									icon={market.config.icons.backArrow}
								/>

								<h3>{t("profile_two")}</h3>
							</div>
						</div>
						<div
							className={`container ${
								market.config.home !== 5
									? `content-container`
									: ``
							}`}
						>
							<ul
								className="nav nav-dashboard flex-column mb-3 mb-md-0"
								role="tablist"
							>
								<div className="active show">
									<div className="row profile-info">
										<ul className="menu-list">
											<li
												className={`nav-item ${
													activePanel === 1
														? "active"
														: ""
												}`}
												onClick={() =>
													setActivePanel(1)
												}
											>
												<span className="nav-link">
													{t("active_orders")}
												</span>
											</li>

											<li
												className={`nav-item
															 ${activePanel === 2 ? "active" : ""}
															`}
												onClick={() =>
													setActivePanel(2)
												}
											>
												<span className="nav-link">
													{t("my_cards")}
												</span>
											</li>
											<li
												className={`nav-item ${
													activePanel === 3
														? "active"
														: ""
												}`}
												onClick={() =>
													setActivePanel(3)
												}
											>
												<span className="nav-link">
													{t("my-sells")}
												</span>
											</li>
											{user.personType === "legal" ? (
												<li
													className={`nav-item ${
														activePanel === 4
															? "active"
															: ""
													}`}
													onClick={() =>
														setActivePanel(4)
													}
												>
													<span className="nav-link">
														{t("my-companies")}
													</span>
												</li>
											) : null}

											<li
												className={`nav-item ${
													activePanel === 5
														? "active"
														: ""
												}`}
												onClick={() =>
													setActivePanel(5)
												}
											>
												<span className="nav-link">
													{t("profile")}
												</span>
											</li>

											<li
												className={`nav-item ${
													activePanel === 6
														? "active"
														: ""
												}`}
												onClick={() =>
													setActivePanel(6)
												}
											>
												<span className="nav-link">
													{t("my-address")}
												</span>
											</li>
											<li className="nav-item">
												<Link
													to="/"
													onClick={() => {
														logoutUser();
														emptyCart();
													}}
												>
													<span className="nav-link text-danger">
														{t("exit")}
													</span>
												</Link>
											</li>
										</ul>
										<div
											className={`col-12 ${
												market.config.home !== 5
													? `col-md-9`
													: ``
											}`}
										>
											<div className="tab-pane">
												<div
													className={`${
														activePanel === 1
															? "active"
															: "d-none"
													}`}
												>
													<DashboardTwo />
												</div>
												<div
													className={`${
														activePanel === 2
															? "active"
															: "d-none"
													}`}
												>
													<UserCards
														token={token}
														user={user}
													/>
												</div>

												<div
													className={`${
														activePanel === 3
															? "active"
															: "d-none"
													}`}
												>
													<div className="purchases">
														<Purchases
															token={token}
														/>
													</div>
												</div>

												{user.personType === "legal" ? (
													<div
														className={`${
															activePanel === 4
																? "active"
																: "d-none"
														}`}
													>
														<Company user={user} />
													</div>
												) : null}

												{market.config.home === 5 &&
												window.webview ? null : (
													<>
														<div
															className={`${
																activePanel ===
																5
																	? "active"
																	: "d-none"
															}`}
														>
															<Settings
																user={user}
																token={token}
															/>
														</div>

														<div
															className={`${
																activePanel ===
																6
																	? "active"
																	: "d-none"
															}`}
														>
															<Addresses
																user={user}
																token={token}
															/>
														</div>
													</>
												)}
											</div>
										</div>
									</div>
								</div>
							</ul>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}

export default React.memo(
	connect(
		state => ({
			user: state.user.user,
			token: state.user.tokenData?.token
		}),
		{
			emptyCart,
			logoutUser
		}
	)(Profile)
);
