import React, { useEffect } from "react";
import Modal from "react-modal";
import { getPrice } from "../../../utils/oxMethods";
import { useTranslation } from "react-i18next";
import car from "../../../assets/images/Vector.png";
import { formatterCurrency } from "../../../utils/string";
import SellRecordTwo from "../../../views/profile/dashboard/SellRecordTwo";
import { useSelector } from "react-redux";

const customStyles = {
	content: {
		maxWidth: "80%",
		top: "50%",
		transform: "translateY(-50%)",
		padding: "20px"
	},
	overlay: {
		backgroundColor: "rgba(77,77,77,0.6)",
		zIndex: "10000"
	}
};

export default function({ modal, setModal }) {
	const sell = modal.sell;

	if (!modal.open) {
		return "";
	}

	const { t } = useTranslation();

	let timer;
	const paidPaymentMethod = sell?.cashdesk?.paymentMethods.find(
		p => p.id === sell.payments[0].paymentMethod
	);
	const { shippingServices } = useSelector(state => state.marketData);

	const shippingService = shippingServices.find(
		s => s.id === sell.shippingService?.id
	);

	function closeModal() {
		document
			.getElementById("order-details")
			.classList.remove("ReactModal__Content--after-open");

		timer = setTimeout(() => {
			setModal({ open: false, product: {} });
		}, 200);
	}
	useEffect(() => {
		return () => {
			if (timer) clearTimeout(timer);
		};
	});

	return (
		<Modal
			isOpen={modal.open}
			className="modal-dialog"
			onRequestClose={closeModal}
			style={customStyles}
			id="order-details"
		>
			{modal.open && (
				<div>
					<div className="modal-content">
						<div className="orders_header">
							<p
								style={{ cursor: "pointer" }}
								className="exit-product-page"
								onClick={() => closeModal()}
							>
								<i className="icon-arrow-left"></i>
							</p>
							<h3>{t("order-details")}</h3>
						</div>
						<div className="order-content">
							<div className="info-order-item">
								<div className="info_order">
									<div className="info-order-content">
										{shippingService ? (
											<div className="delivery_type">
												<img
													src={car}
													alt="car"
													width="20"
													height="20"
												/>
												<p className="delivery_name">
													{shippingService?.name}
												</p>
												{shippingService?.price ? (
													shippingService?.price[
														shippingService?.price
															.first
													] > 0 ? (
														<p className="delivery_price ml-2">
															{formatterCurrency(
																shippingService
																	?.price[
																	shippingService
																		?.price
																		.first
																]
															)}
														</p>
													) : (
														<p className="delivery_price ml-2">
															{formatterCurrency(
																shippingService
																	?.price[
																	shippingService
																		?.price
																		.first
																]
															)}
														</p>
													)
												) : (
													""
												)}
											</div>
										) : null}

										<div className="sell_group">
											<div className="info_sell">
												<p className="sell-id m-0">
													{t("order")} #{sell.id}
												</p>
											</div>
											<div className="sell_items">
												{sell.refundRecords.length > 0
													? sell.refundRecords.map(
															(
																sellRecord,
																index
															) => {
																return (
																	<SellRecordTwo
																		key={
																			index
																		}
																		refundRecord={
																			sellRecord
																		}
																		sellRecord={
																			sellRecord
																		}
																	/>
																);
															}
													  )
													: sell.sellRecords
															.filter(
																record =>
																	!record.deleted
															)
															.map(
																(
																	sellRecord,
																	index
																) => {
																	return (
																		<SellRecordTwo
																			key={
																				index
																			}
																			sellRecord={
																				sellRecord
																			}
																		/>
																	);
																}
															)}
											</div>
										</div>
									</div>
								</div>
								<div className="summary">
									<div className="table-wrapper">
										<div className="table table-summary">
											<div className="summary-subtotal">
												<p>{t("productstwo")}</p>
												<p>
													{getPrice(
														sell?.total,
														true
													)}
												</p>
											</div>
											{sell?.shippingService?.price ? (
												<div className="summary-shipping">
													<p>{t("delivery")}</p>
													<p>
														{formatterCurrency(
															sell.shippingService
																?.price[
																sell
																	?.shippingService
																	.price.first
															]
														)}
													</p>
												</div>
											) : (
												""
											)}
										</div>
										{sell.shippingAddress ? (
											<div className="location">
												<p>
													{
														sell?.shippingAddress
															?.addressLine1
													}
												</p>
											</div>
										) : null}
										<div className="payment">
											<p>{t("payment")}:</p>
											<p>
												<img
													src={`https://app.ox-sys.com${paidPaymentMethod?.icon}`}
													alt="card"
													width="25"
													height="20"
												/>
												<span>
													{paidPaymentMethod.name}
												</span>
											</p>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			)}
		</Modal>
	);
}
