import React, { Fragment, useState } from "react";
import { useTranslation } from "react-i18next";
import { Field, reduxForm } from "redux-form";
import market from "../../../config/market";
const validate = values => {
	const errors = {};

	if (!values.firstName) {
		errors.firstName = " ";
	} else if (values.firstName.length < 2 || values.firstName.length > 50) {
		errors.firstName =
			"     15   ";
	}

	if (!values.lastName) {
		errors.lastName = " ";
	} else if (values.firstName.length < 2 || values.firstName.length > 50) {
		errors.firstName =
			"     15   ";
	}

	if (!values.number) {
		errors.number = "    7   ";
	}

	if (values.password1 && values.password1.trim().length < 4) {
		errors.password1 = "    4   ";
	}
	// else if (values.password2 && values.password2.trim().length < 4) {
	// 	errors.password2 = "    4   ";
	// } else if (!(values.password1 === values.password2)) {
	// 	errors.password2 = " ";
	// }
	return errors;
};

const field = props => {
	let {
		input,
		type,
		className,
		placeholder,
		checked,
		meta: { touched, error }
	} = props;

	if (touched) {
		if (error) {
			className = className + " warning";
		} else {
			className = className + " correct";
		}
	}
	return (
		<Fragment>
			<input
				{...input}
				placeholder={placeholder}
				className={className}
				type={type}
				checked={checked}
			/>
			{touched && error && <span className="d-block">{error}</span>}
		</Fragment>
	);
};
function EditForm({ handleSubmit, user }) {
	const [personType, setPersonType] = useState(user.personType);
	const { t } = useTranslation();

	return (
		<>
			<form onSubmit={handleSubmit} className="edit_user">
				<h3 className="edit-user-title">{t("my_info")}</h3>
				<div className="d-flex align-items-center radios">
					<label
						onClick={() => setPersonType("physical")}
						htmlFor="physical"
						className="d-flex align-items-center mx-2"
						style={{
							cursor: "pointer"
						}}
					>
						<Field
							type="radio"
							component={field}
							id="physical"
							name="personType"
							value="physical"
							checked={personType === "physical" ? true : false}
						/>
						<span className="ml-2">{t("physical-person")}</span>
					</label>
					<label
						onClick={() => setPersonType("legal")}
						htmlFor="legal"
						className="d-flex align-items-center mx-2"
						style={{
							cursor: "pointer"
						}}
					>
						<Field
							type="radio"
							component={field}
							id="legal"
							name="personType"
							value="legal"
							checked={personType === "legal" ? true : false}
						/>
						<span className="ml-2">{t("legal-person")}</span>
					</label>
				</div>
				<div className="row">
					<div className="col-sm-6">
						<label>{t("name")}*</label>
						<Field
							type="first name"
							className="form-control"
							id="username"
							placeholder=""
							component={field}
							name="firstName"
							minLength={5}
							required
						/>
					</div>

					<div className="col-sm-6">
						<label>{t("surname")} *</label>
						<Field
							type="last name"
							className="form-control"
							id="username"
							placeholder=""
							component={field}
							name="lastName"
							minLength={5}
							required
						/>
					</div>
				</div>

				<div className="row">
					<div className="col-12">
						<h3 className="d-none">{t("contacts")}</h3>
					</div>
					<div className="col-md-6 col-12">
						<label>{t("phone-number")} *</label>
						<Field
							type="phone"
							className="form-control"
							id="number"
							component={field}
							placeholder=" "
							name="number"
							minLength={7}
							maxLength={13}
							required
						/>
					</div>

					<div className="col-md-6 col-12">
						<label>{t("email")} *</label>
						<Field
							type="email"
							className="form-control"
							id="email"
							placeholder=" "
							component={field}
							name="email"
						/>
					</div>
				</div>
				<div className="row">
					<div className="col-12">
						<h3 className="d-none">{t("change_pass")}</h3>
					</div>
					<div className="col-md-6 col-12">
						<label>
							{market.config.home === 8
								? t("new-password2")
								: t("new-password")}
						</label>
						<Field
							type="password"
							className="form-control"
							id="password"
							placeholder=" "
							component={field}
							minLength={6}
							name="password1"
						/>
					</div>

					{/* <div className="col-md-6 col-12">
						<label>{t("repeat-pass")}</label>
						<Field
							type="password"
							className="form-control"
							id="password"
							placeholder=" "
							component={field}
							minLength={6}
							name="password2"
						/>
					</div> */}
				</div>
				<button
					type="submit"
					className="d-block btn btn-outline-primary-2"
				>
					<span>{t("save-edits")}</span>
					<i className="icon-long-arrow-right"></i>
				</button>
			</form>
		</>
	);
}

export default reduxForm({ form: "editForm", validate })(EditForm);
