import React, { useState } from "react";
import { setUserData } from "../../../actions/user";
import { updateUser } from "../../../api/user";
import { isEmail } from "../../../utils/string";
import EditForm from "./EditForm";
import Loader from "../../../components/features/loaders/loader";
import { useDispatch, useSelector } from "react-redux";

const Profile = () => {
	const dispatch = useDispatch();
	const { user } = useSelector(state => state.user);
	const token = useSelector(state => state.user.tokenData);
	const [loading, setLoading] = useState(false);
	const personType = user.personType;

	const submit = values => {
		let firstName = values.firstName.replace(/ /g, "");
		let lastName = values.lastName.replace(/ /g, "");
		let email = isEmail(values.email) ? values.email : false;

		let number = values.number.trim().split("");
		let numbers = "";
		for (let i of number) {
			if (parseInt(i) >= 0) {
				numbers = numbers += parseInt(i);
			}
		}

		let newUser = {
			profile: {
				firstName: firstName,
				lastName: lastName,
				phoneNumber: numbers,
				gender: "male"
			},
			personType: values.personType ? values.personType : personType
		};

		if (
			values.password1
			//  && values.password2
		) {
			newUser.password = {
				first: values.password1,
				second: values.password1
			};
		}

		if (email) {
			newUser.email = email;
		}

		if (newUser) {
			setLoading(true);
			updateUser(newUser).then(res => {
				if (res) {
					dispatch(setUserData(res, token));
					setLoading(false);
				}
			});
		}
	};

	const initialValues = {
		firstName: user.profile.firstName,
		lastName: user.profile.lastName,
		number: user.profile.phoneNumbers[0],
		email: user.email && user.email
	};

	return loading ? (
		<Loader />
	) : (
		<EditForm initialValues={initialValues} onSubmit={submit} user={user} />
	);
};

export default Profile;
