import React, { useState } from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import OrderDetailsModal from "../../../components/features/modal/OrderDetailsModal";
import market from "../../../config/market";
import OrdersItemThree from "./OrderItemThree";
import OrdersItemTwo from "./OrdersItemTwo";

export default function OrdersListTwo({ deliveryGroups }) {
	const { t } = useTranslation();
	const [order, setOrder] = useState(0);

	return (
		<div className="dashboard-sells">
			{deliveryGroups.length > 0 ? (
				deliveryGroups.map((item, key) =>
					market.config.home === 5 || market.config.home === 8 ? (
						<OrdersItemThree
							setOrder={setOrder}
							order={item}
							key={key}
						/>
					) : (
						<OrdersItemTwo
							setOrder={setOrder}
							order={item}
							key={key}
						/>
					)
				)
			) : (
				<div className="no-data">
					<h3>{t("no-active-orders")}</h3>
					<img
						src="/assets/images/wooz/no-info.png"
						alt="no-data"
						className="no-data-img"
					/>
					<Link to="/" className="btn shopping-button">
						{t("go-to-shopping")}
					</Link>
				</div>
			)}
			{order ? <OrderDetailsModal prop={order} /> : null}
		</div>
	);
}
