import i18n from "../../../i18n";

const validate = values => {
	const errors = {};
	if (!values.firmProfile || !values.firmProfile.length) {
		errors.firmProfile = { _error: "eeee" };
	} else {
		const firmProfileErrors = [];
		values.firmProfile.forEach((firm, firmIndex) => {
			const firmErrors = {};

			if (!firm || !firm.firmName) {
				firmErrors.firmName = i18n.t("required");
				firmProfileErrors[firmIndex] = firmErrors;
			}
			if (!firm || !firm.legalName) {
				firmErrors.legalName = i18n.t("required");
				firmProfileErrors[firmIndex] = firmErrors;
			}

			if (!firm || !firm.okonh) {
				firmErrors.okonh = i18n.t("required");
				firmProfileErrors[firmIndex] = firmErrors;
			}

			if (!firm || !firm.inn) {
				firmErrors.inn = i18n.t("required");
				firmProfileErrors[firmIndex] = firmErrors;
			} else if (firm.inn.length !== 9) {
				firmErrors.inn = i18n.t("    9");
				firmProfileErrors[firmIndex] = firmErrors;
			}

			if (!firm || !firm.account) {
				firmErrors.account = i18n.t("required");
				firmProfileErrors[firmIndex] = firmErrors;
			}

			if (!firm || !firm.bank) {
				firmErrors.bank = i18n.t("required");
				firmProfileErrors[firmIndex] = firmErrors;
			}

			if (!firm || !firm.mainAddress) {
				firmErrors.mainAddress = i18n.t("required");
				firmProfileErrors[firmIndex] = firmErrors;
			}

			if (!firm || !firm.legalAddress) {
				firmErrors.legalAddress = i18n.t("required");
				firmProfileErrors[firmIndex] = firmErrors;
			}

			if (!firm || !firm.mfo) {
				firmErrors.mfo = i18n.t("required");
				firmProfileErrors[firmIndex] = firmErrors;
			}
		});
		if (firmProfileErrors.length) {
			errors.firmProfile = firmProfileErrors;
		}
	}
	return errors;
};

export default validate;
