import React, { useEffect, useState } from "react";
import { formatterCurrency } from "../../../utils/string";
import { distanceApi, driverApi } from "../../../api/mainCaller";
import car from "../../../assets/images/VectorCar.png";
import time from "../../../assets/images/Time.png";
import userpng from "../../../assets/images/user.png";
import YandexMapWithRoute from "../../../components/features/YandexMapWithRoute";
import allStatus from "./status";
import { useDispatch, useSelector } from "react-redux";
import { openModal } from "../../../actions/modal";
import { groupBy } from "../../../utils/array";
import { useTranslation } from "react-i18next";
import isSuccessDate from "../../checkout/sells/isSuccessDate";

function OrdersItemTwo({ order, setOrder }) {
	const { shippingServices } = useSelector(state => state.marketData);
	const dispatch = useDispatch();
	// const { token } = useSelector(state => state.user.tokenData);
	const shippingService = shippingServices.find(
		s => s.id === order.sells[0].shippingService
	);
	const { t } = useTranslation();
	let total = order.sells.reduce(
		(prev, n) => [parseInt(prev) + parseInt(n.total[n.total.first])],
		[0]
	);

	const driverInfo = order.options.driver; //delivery.driver;
	const status = Array.from(groupBy(order.sells, prop => prop.status));
	const [currentStatus] = useState(
		status.map(statusOne => {
			return {
				name: allStatus.find(s => s.key === statusOne[0])?.name,
				key: allStatus.find(s => s.key === statusOne[0])?.key,
				bg_color: allStatus.find(s =>
					s.key === statusOne[0] ? s.bg_color : ""
				),
				color: allStatus.find(s =>
					s.key === statusOne[0] ? s.color : ""
				)
			};
		})
	);
	const [curier, setCurier] = useState({
		center: [null, null],
		zoom: 16
	});
	const [heading, setHeading] = useState(null);
	const [distance, setDistance] = useState();

	function paymentStatus(status) {
		return status === "pending" ? "" : "";
	}
	async function driver() {
		if (order.options.delivery) {
			const res = await driverApi(
				order.options.delivery,
				order.options.token
			);
			if (res) {
				setCurier({
					...curier,
					center: [res.latitude, res.longitude]
				});
				setHeading(res.heading);
			}
		}
	}
	function openModalCheck() {
		setOrder({
			order,
			total,
			shippingService
		});
		dispatch(openModal("order-details"));
	}

	async function getDistance() {
		if (order.options.delivery) {
			const res = await distanceApi(
				order.options.delivery,
				order.options.token
			);
			if (res) {
				setDistance(res);
			}
		}
	}

	let set = setTimeout(() => {
		driver();
	}, 5000000);

	useEffect(() => {
		getDistance();
		driver();
		return () => {
			clearTimeout(set);
		};
	}, []);

	const shippingTime = order.sells[0].time.split(" ")[1].split(":")[0];
	return (
		<div className="dashboard-sells-groups">
			<h3 className="sell_title">{t("current-order")}</h3>
			<div className="row delivery_content">
				<div className="col-md-6 col-12">
					<div className="sells-group">
						<div className="sell_group_info">
							<div className="sell_group_info_header">
								<span>{t("orders")}</span>
								<div className="orders">
									{order.sells.map(sell => {
										return (
											<span key={sell.id}>
												#{sell.id}
											</span>
										);
									})}
									<div className="status-wrapper">
										{isSuccessDate(
											parseInt(shippingTime)
										) ? (
											<div
												className="pending"
												style={{
													marginLeft: "5px",
													fontSize: "11px",
													lineHeight: "23px",
													backgroundColor:
														"rgb(252, 126, 126)",
													borderRadius: "5px",
													color: "rgb(255, 255, 255)",
													padding: "0px 7px"
												}}
											>
												
											</div>
										) : (
											currentStatus.map(
												(currentStatus, i) => (
													<p
														key={i}
														style={{
															backgroundColor:
																currentStatus
																	?.bg_color
																	.bg_color,
															color:
																currentStatus
																	?.color
																	.color,

															border: `1px solid ${`${currentStatus?.color.color}`}`,
															padding: "4px",
															borderRadius: "5px"
														}}
														className={`order-status ${currentStatus?.key?.replace(
															/ /g,
															"_"
														)}`}
													>
														{currentStatus.name}
													</p>
												)
											)
										)}
									</div>
								</div>
							</div>
							<div className="sell_group_info_body">
								<div className="delivery_type">
									<img
										src={car}
										alt="car"
										width="20"
										height="20"
									/>
									<p className="delivery_name">
										{shippingService?.name}
									</p>
									{shippingService?.price ? (
										shippingService.price[
											shippingService.price.first
										] > 0 ? (
											<div className="body-panding">
												<span
													className={
														order.sells[0]
															.payments[0]?.status
													}
												>
													{paymentStatus(
														order.sells[0]
															.payments[0]?.status
													)}
												</span>
												<p className="delivery_price">
													{formatterCurrency(
														shippingService.price[
															shippingService
																.price.first
														]
													)}
												</p>
											</div>
										) : (
											<p
												className="delivery_price"
												style={{
													flexGrow: "1",
													textAlign: "right"
												}}
											>
												{formatterCurrency(
													shippingService.price[
														shippingService.price
															.first
													]
												)}
											</p>
										)
									) : (
										""
									)}
								</div>
								{!shippingService?.pickup && (
									<div className="delivery_time">
										{typeof distance === "string" ? (
											order.status === "pending" ? (
												<p className="ml-auto">
													  ...
												</p>
											) : null
										) : (
											<>
												<img
													src={time}
													alt="time"
													width="20"
													height="20"
												/>
												<p className="delivery_time_name">
													{t("soon-time")}
												</p>
												<p className="delivery_time_current">
													{distance?.duration}&nbsp;
													{t("minute2")}
												</p>
											</>
										)}
									</div>
								)}
								{driverInfo ? (
									<>
										<div className="delivery_courier">
											<img
												src={userpng}
												alt="user"
												width="20"
												height="20"
											/>
											<p className="courier_title">
												{t("courier")}
											</p>
											<p className="courier_name">
												{driverInfo.full_name || "-"}
											</p>
										</div>
										<div className="delivery_car">
											<img
												src={time}
												alt="time"
												width="20"
												height="20"
											/>
											<p className="delivery_car_title">
												{t("transport")}
											</p>
											<p className="delivery_car_name">
												{driverInfo.car.mark || "-"}
											</p>
										</div>
										{driverInfo.car.number ? (
											<div className="delivery_car_number text-right">
												<span className="car_number">
													{driverInfo.car.number}
												</span>
											</div>
										) : null}
									</>
								) : null}
							</div>
							<div className="sell_group_info_footer">
								<button
									// href={`/order-details/${token}/${order.id}`}
									className="check"
									onClick={openModalCheck}
								>
									{t("view-receipt")}
								</button>
								<div>
									<p
										className={
											order.sells[0].payments[0]?.status
										}
									>
										{paymentStatus(
											order.sells[0].payments[0]?.status
										)}
									</p>
									<p className="price m-0">
										{formatterCurrency(total[0])}
									</p>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div
					className="col-md-6 col-12 map"
					style={{
						borderRadius: "10px",
						overflow: "hidden",
						position: "relative"
					}}
				>
					{/* {isSuccessDate(parseInt(shippingTime)) ? (
						isSuccessDate(parseInt(shippingTime))
					) :null} */}

					{currentStatus[0].key !== allStatus[9].key &&
					curier.center[0] ? (
						<YandexMapWithRoute
							mapOptions={curier}
							heading={heading}
							route={[
								curier.center,
								order.shippingAddress.latitude
									? [
											order.shippingAddress.latitude,
											order.shippingAddress.longitude
									  ]
									: order.shippingAddress.addressLine1
							]}
						/>
					) : currentStatus[0].key === allStatus[1].key ||
					  currentStatus[0].key === allStatus[0].key ? (
						<img
							className="w-100"
							src="/assets/images/wooz/aborted.svg"
							alt="aborted"
						/>
					) : null}
				</div>
			</div>
		</div>
	);
}

export default OrdersItemTwo;
