import React, { useEffect, useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import { logoutUser, setUserData } from "../../actions/user";
import { emptyCart } from "../../actions/cart";
import Purchases from "./purchases/Purchases";
import Settings from "./settings/Settings";
import Company from "./Company/Company";
import Addresses from "./addresses/addresses";
import { useTranslation } from "react-i18next";
import DashboardTwo from "./dashboard/DashboardTwo";
import { getUserByToken } from "../../api/user";
import LoaderOverlay from "../../components/features/loaders/LoaderOverlay";
import market from "../../config/market";
import RiveLoader from "../../components/features/loaders/RiveLoader";
import UserCards from "./userCards/UserCards";
import { Link } from "react-router-dom";

function Profile({ user, logoutUser, emptyCart, token }) {
	const tokenData = useSelector(state => state.user.tokenData);
	const { t } = useTranslation();
	const dispatch = useDispatch();
	const [activePanel, setActivePanel] = useState(1);

	const { settings } = useSelector(state => state.marketData);

	useEffect(() => {
		document.querySelector("body").classList.add("profile");

		if (token && !user.username) {
			getUserByToken(token).then(res => {
				dispatch(setUserData(res, tokenData));
			});
		}
		return () => {
			document.querySelector("body").classList.remove("profile");
		};
	}, []);

	return !user.username ? (
		market.config.loader ? (
			<RiveLoader />
		) : (
			<LoaderOverlay />
		)
	) : (
		<>
			<div className="main profile-one">
				<div className="page-content">
					<div className="profile-nav">
						<div className="container">
							<ul>
								<li
									className={`nav-item ${
										activePanel === 1 ? "active" : ""
									}`}
									onClick={() => setActivePanel(1)}
								>
									<span className="nav-link">
										{t("dashboard")}
										<i className="icon-angle-right d-none"></i>
									</span>
								</li>

								<li
									className={`nav-item
															${activePanel === 2 ? "active" : ""}
															`}
									onClick={() => setActivePanel(2)}
								>
									<span className="nav-link">
										{t("my_cards")}
										<i className="icon-angle-right d-none"></i>
									</span>
								</li>
								<li
									className={`nav-item ${
										activePanel === 3 ? "active" : ""
									}`}
									onClick={() => setActivePanel(3)}
								>
									<span className="nav-link">
										{t("my-sells")}
										<i className="icon-angle-right d-none"></i>
									</span>
								</li>
								{user.personType === "legal" ? (
									<li
										className={`nav-item ${
											activePanel === 4 ? "active" : ""
										}`}
										onClick={() => setActivePanel(4)}
									>
										<span className="nav-link">
											{t("my-companies")}
										</span>
									</li>
								) : null}

								<li
									className={`nav-item ${
										activePanel === 5 ? "active" : ""
									}`}
									onClick={() => setActivePanel(5)}
								>
									<span className="nav-link">
										{t("profile")}
										<i className="icon-angle-right d-none"></i>
									</span>
								</li>

								<li
									className={`nav-item ${
										activePanel === 6 ? "active" : ""
									}`}
									onClick={() => setActivePanel(6)}
								>
									<span className="nav-link">
										{t("my-address")}
										<i className="icon-angle-right d-none"></i>
									</span>
								</li>
								<li className="nav-item exit">
									<Link
										to="/"
										onClick={() => {
											logoutUser();
											emptyCart();
										}}
										className="nav-link text-danger"
									>
										{t("exit")}
									</Link>
								</li>
							</ul>
						</div>
					</div>

					<div className="container">
						<div className="row profile-info">
							<div className="col-12">
								<div className="tab-pane">
									<div
										className={`${
											activePanel === 1
												? "active"
												: "d-none"
										}`}
									>
										<DashboardTwo />
										{settings.phoneNumbers ? (
											<div className="info_support only-mobile">
												{/* <span>{t("have-questions")}</span> */}
												<a
													href={`tel:${settings.phoneNumbers[0]}`}
												>
													<i className="icon icon-headphones"></i>
													{t("call-center")}
												</a>
											</div>
										) : null}
									</div>
									<div
										className={`${
											activePanel === 2
												? "active"
												: "d-none"
										}`}
									>
										<UserCards token={token} user={user} />
									</div>

									<div
										className={`${
											activePanel === 3
												? "active"
												: "d-none"
										}`}
									>
										<div className="purchases">
											<Purchases token={token} />
										</div>
									</div>

									{user.personType === "legal" ? (
										<div
											className={`${
												activePanel === 4
													? "active"
													: "d-none"
											}`}
										>
											<Company user={user} />
										</div>
									) : null}

									<div
										className={`${
											activePanel === 5
												? "active"
												: "d-none"
										}`}
									>
										<Settings user={user} token={token} />
									</div>

									<div
										className={`${
											activePanel === 6
												? "active"
												: "d-none"
										}`}
									>
										<Addresses user={user} token={token} />
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}

export default React.memo(
	connect(
		state => ({
			user: state.user.user,
			token: state.user.tokenData?.token
		}),
		{
			emptyCart,
			logoutUser
		}
	)(Profile)
);
