import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getDeliveryGroups } from "../../../api/user";
import LoadingOverlay from "../../../components/features/loaders/LoaderOverlay";
import OrdersListTwo from "./OrdersListTwo";
import market from "../../../config/market";
import RiveLoader from "../../../components/features/loaders/RiveLoader";
import { logoutUser } from "../../../actions/user";

function DashboardTwo() {
	const dispatch = useDispatch();
	const { token } = useSelector(state => state.user.tokenData);
	const [loading, setLoading] = useState(true);
	const [pendingOrder, setPendingOrder] = useState([]);

	function getDeliveryGroup() {
		getDeliveryGroups(token)
			.then(sellPage => {
				if (sellPage) {
					const pendingSell = sellPage.items.filter(
						sell => sell.status === "pending"
					);
					if (pendingSell.length) setPendingOrder(pendingSell);
					setLoading(false);
				}
			})
			.catch(e => {
				if (e.response.status === 401) {
					dispatch(logoutUser());
				}
			});
	}

	useEffect(() => {
		getDeliveryGroup();
	}, []);

	return (
		<div className="row">
			<div className="col-12 dashboard-banner">
				<div className="banner banner-big">
					{loading ? (
						market.config.loader ? (
							<RiveLoader />
						) : (
							<LoadingOverlay />
						)
					) : pendingOrder ? (
						<OrdersListTwo deliveryGroups={pendingOrder} />
					) : null}
				</div>
			</div>
		</div>
	);
}

export default DashboardTwo;
