import React, { useEffect, useState } from "react";
import { getUserSellList } from "../../../api/user";
import { getPrice } from "../../../utils/oxMethods";
import status from "../status";
import timee from "../../../assets/images/Time.png";
import SellModal from "../../../components/features/modal/SellModal";
import LoadingOverlay from "../../../components/features/loaders/LoaderOverlay";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import market from "../../../config/market";
import SellModalTwo from "../../../components/features/modal/SellModalTwo";

export default function({ token }) {
	const { t } = useTranslation();
	const [orders, setOrders] = useState([]);
	const [loading, setLoading] = useState(false);
	const [modal, setModal] = useState({
		open: false,
		sell: {},
		status: ""
	});

	function getSells() {
		setLoading(true);
		getUserSellList(1, token).then(res => {
			setOrders(res.items);
			setLoading(false);
		});
	}

	useEffect(() => {
		getSells();
	}, []);

	return (
		<>
			{loading ? (
				<LoadingOverlay />
			) : orders.length > 0 ? (
				orders.map((order, i) => {
					return (
						<div key={i} className="dashboard-sells-groups">
							<div className="col-12">
								<div className="title-order">
									<h3>{t(" ")}</h3>
								</div>
								<div className="sells-group">
									<div className="sell_group_info">
										<div className="sell_group_info_header d-flex justify-content-between w-100 p-0">
											<div className="orders d-block">
												<div className="sell-group-header d-flex align-items-center">
													<span className="order-id">
														#{order.id}
													</span>
													<div className="status-wrapper">
														{order.refundRecords
															.length > 0 ? (
															<p className="text-white bg-dark">
																{t("return")}
															</p>
														) : (
															status.map(
																(
																	status,
																	index
																) =>
																	status.name ===
																	order.status ? (
																		<p
																			key={
																				index
																			}
																			className="text-white"
																			style={{
																				backgroundColor:
																					status.background
																			}}
																		>
																			{
																				status.translate
																			}
																		</p>
																	) : null
															)
														)}
													</div>
												</div>
												{!order.shippingService
													?.pickup && (
													<div className="delivery_time d-flex align-items-center">
														<img
															src={timee}
															alt="time"
															width="20"
															height="20"
														/>
														<p className="delivery_time_current pl-2">
															{order?.startTime?.slice(
																0,
																order.startTime
																	.length - 3
															)}
														</p>
													</div>
												)}
											</div>
											<div className="sell_group_info_footer p-0">
												<div className="price-count">
													<p className={`price`}>
														{getPrice(
															order.total,
															true
														)}
													</p>
													<p className="order-count">
														{order.count}
														&nbsp;
														{t("pc")}
													</p>
												</div>
												<div className="view-check">
													<button
														className="check-button"
														onClick={() =>
															setModal({
																open: true,
																sell: order
															})
														}
													>
														<img
															src={
																market.config
																	.icons
																	.orderView
															}
															alt="view-icon"
														/>
													</button>
												</div>
											</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					);
				})
			) : (
				<div className="no-data">
					<h3>{t("no-active-orders")}</h3>
					<img src="/assets/images/wooz/no-info.png" alt="no-data" />
					<Link to="/" className="btn shopping-button">
						{t("go-to-shopping")}
					</Link>
				</div>
			)}
			{market.config.home === 5 ? (
				<SellModalTwo modal={modal} setModal={setModal} />
			) : (
				<SellModal modal={modal} setModal={setModal} />
			)}
		</>
	);
}
