import React, { useState } from "react";
import { Link } from "react-router-dom";
import { connect, useDispatch, useSelector } from "react-redux";
import { logoutUser, setUserData } from "../../actions/user";
import { emptyCart } from "../../actions/cart";
import Purchases from "./purchases/Purchases";
import Settings from "./settings/Settings";
import Company from "./Company/Company";
import Addresses from "./addresses/addresses";
import { useTranslation } from "react-i18next";
import DashboardTwo from "./dashboard/DashboardTwo";
import BackButton from "../../components/common/BackButton";
import { LazyLoadImage } from "react-lazy-load-image-component";
import { formatterCurrency } from "../../utils/string";
import pen from "../../assets/images/Vector2.png";
import { getUserByToken } from "../../api/user";
import LoaderOverlay from "../../components/features/loaders/LoaderOverlay";
import market from "../../config/market";
import RiveLoader from "../../components/features/loaders/RiveLoader";
import UserCards from "./userCards/UserCards";

function Profile({ user, logoutUser, emptyCart, token }) {
	const tokenData = useSelector(state => state.user.tokenData);
	const { t } = useTranslation();
	const [loading, setLoading] = useState(false);
	const dispatch = useDispatch();
	const [activePanel, setActivePanel] = useState(1);
	let hour = new Date().getHours();
	const { settings } = useSelector(state => state.marketData);

	async function reloadCash() {
		setLoading(true);
		setTimeout(() => {
			getUserByToken(token).then(res => {
				dispatch(setUserData(res, tokenData));
			});
			setLoading(false);
		}, 3000);
	}

	return !user.username ? (
		market.config.loader ? (
			<RiveLoader />
		) : (
			<LoaderOverlay />
		)
	) : (
		<>
			<div
				className={
					"main profile-two" +
					(market.config.home === 5 ? "" : " new-profile-style")
				}
			>
				<div className="page-content">
					<div className="dashboard">
						<div className="dashboard_header">
							<BackButton icon={market.config.icons.backArrow} />
							<h3>
								{market.config.home === 5
									? t("profile")
									: t("dashboard")}
							</h3>
						</div>
						<div
							className={`container ${
								market.config.home !== 5
									? `content-container`
									: ``
							}`}
						>
							<div
								className={`row info_user ${
									market.config.home !== 5
										? `old-user-info`
										: ``
								}`}
							>
								<div className="col-4 dashboard-banner">
									<div className="banner banner-big">
										<div className="lazy-overlay-image">
											<LazyLoadImage
												alt="current-order"
												src={`${process.env.PUBLIC_URL}/assets/images/profile-image.png`}
												threshold={100}
												effect="black-and-white"
												style={{
													objectFit: "cover"
												}}
											/>
										</div>
										<div className="banner-content image-content">
											<h4 className="banner-subtitle">
												{hour >= 6 && hour < 11
													? t("good-morning")
													: hour >= 11 && hour < 17
													? t("good-day")
													: hour >= 17 && hour < 22
													? t("good-evening")
													: t("good-night")}
											</h4>
											<h4 className="banner-subtitle">
												{user.profile.firstName}
											</h4>
											<button
												className="btn-edit"
												onClick={() => {
													setActivePanel(5);
												}}
											>
												<img
													src={pen}
													alt="pen"
													width="20"
													height="20"
												/>
												<span>{t("change")}</span>
											</button>
										</div>
									</div>
								</div>
								<div className="col-4">
									<div
										className="banner balance"
										style={{
											backgroundColor: "#F0F0F0"
										}}
									>
										<div className="banner-content w-100">
											<h4 className="banner-subtitle">
												{t("balance")}
												<button
													className={`reload-cash ${
														loading ? `loading` : ``
													}`}
													onClick={reloadCash}
												>
													<img
														src="/assets/images/icons/reload.svg"
														alt="refresh"
													/>
												</button>
											</h4>
											<h3 className="d-p-title">
												{/* {console.log(user.wallet)} */}
												{formatterCurrency(
													user.wallet
														? user.wallet.totalCash
														: 0
												)}
											</h3>
										</div>
									</div>
								</div>
							</div>
							<ul
								className="nav nav-dashboard flex-column mb-3 mb-md-0"
								role="tablist"
							>
								<div className="active show">
									<div className="row profile-info">
										<aside
											className={`col-12 p-5 ${
												market.config.home !== 5
													? `col-md-3`
													: ``
											}`}
										>
											{market.config.home !== 5 ? (
												<div className="row info_user new-user-info">
													<div className="col-4 dashboard-banner col-md-12">
														<div className="banner banner-big">
															<div className="lazy-overlay-image">
																<LazyLoadImage
																	alt="current-order"
																	src={`${process.env.PUBLIC_URL}/assets/images/profile-image.png`}
																	threshold={
																		100
																	}
																	effect="black-and-white"
																	style={{
																		objectFit:
																			"cover"
																	}}
																/>
															</div>
															<div className="banner-content image-content">
																<h4 className="banner-subtitle">
																	{hour >=
																		6 &&
																	hour < 11
																		? t(
																				"good-morning"
																		  )
																		: hour >=
																				11 &&
																		  hour <
																				17
																		? t(
																				"good-day"
																		  )
																		: hour >=
																				17 &&
																		  hour <
																				22
																		? t(
																				"good-evening"
																		  )
																		: t(
																				"good-night"
																		  )}
																</h4>
																<h4 className="banner-subtitle">
																	{
																		user
																			.profile
																			.firstName
																	}
																</h4>
																<button
																	className="btn-edit"
																	onClick={() => {
																		setActivePanel(
																			5
																		);
																	}}
																>
																	<img
																		src={
																			pen
																		}
																		alt="pen"
																		width="20"
																		height="20"
																	/>
																	<span>
																		{t(
																			"change"
																		)}
																	</span>
																</button>
															</div>
														</div>
													</div>
													<div className="col-4 col-md-12">
														<div
															className="banner balance"
															style={{
																backgroundColor:
																	"#F0F0F0"
															}}
														>
															<div className="banner-content w-100">
																<h4 className="banner-subtitle">
																	{t(
																		"balance"
																	)}
																	<button
																		className={`reload-cash ${
																			loading
																				? `loading`
																				: ``
																		}`}
																		onClick={
																			reloadCash
																		}
																	>
																		<img
																			src="/assets/images/icons/reload.svg"
																			alt="refresh"
																		/>
																	</button>
																</h4>
																<h3 className="d-p-title">
																	{formatterCurrency(
																		user.wallet
																			? user
																					.wallet
																					.totalCash
																			: 0
																	)}
																</h3>
															</div>
														</div>
													</div>
												</div>
											) : null}
											<div className="profile-dropdown-menu">
												<div className="profile-dropdown-menu-list">
													<ul>
														<li
															className={`nav-item ${
																activePanel ===
																1
																	? "active"
																	: ""
															}`}
															onClick={() =>
																setActivePanel(
																	1
																)
															}
														>
															<span className="nav-link">
																{t("dashboard")}
																<i className="icon-angle-right d-none"></i>
															</span>
														</li>

														<li
															className={`nav-item
															${activePanel === 2 ? "active" : ""}
															`}
															onClick={() =>
																setActivePanel(
																	2
																)
															}
														>
															<span className="nav-link">
																{t("my_cards")}
																<i className="icon-angle-right d-none"></i>
															</span>
														</li>
														<li
															className={`nav-item ${
																activePanel ===
																3
																	? "active"
																	: ""
															}`}
															onClick={() =>
																setActivePanel(
																	3
																)
															}
														>
															<span className="nav-link">
																{t("my-sells")}
																<i className="icon-angle-right d-none"></i>
															</span>
														</li>
														{user.personType ===
														"legal" ? (
															<li
																className={`nav-item ${
																	activePanel ===
																	4
																		? "active"
																		: ""
																}`}
																onClick={() =>
																	setActivePanel(
																		4
																	)
																}
															>
																<span className="nav-link">
																	{t(
																		"my-companies"
																	)}
																</span>
															</li>
														) : null}

														<li
															className={`nav-item ${
																activePanel ===
																5
																	? "active"
																	: ""
															}`}
															onClick={() =>
																setActivePanel(
																	5
																)
															}
														>
															<span className="nav-link">
																{t("profile")}
																<i className="icon-angle-right d-none"></i>
															</span>
														</li>

														<li
															className={`nav-item ${
																activePanel ===
																6
																	? "active"
																	: ""
															}`}
															onClick={() =>
																setActivePanel(
																	6
																)
															}
														>
															<span className="nav-link">
																{t(
																	"my-address"
																)}
																<i className="icon-angle-right d-none"></i>
															</span>
														</li>
														<li>
															<Link
																to="/"
																onClick={() => {
																	logoutUser();
																	emptyCart();
																}}
																className="nav-link text-danger"
															>
																{t("exit")}
															</Link>
														</li>
													</ul>

													{settings &&
													settings.phoneNumbers ? (
														<div className="info_support only-desktop">
															{/* <span>{t("have-questions")}</span> */}
															<a
																href={`tel:${settings.phoneNumbers[0]}`}
															>
																<i className="icon icon-headphones"></i>
																{t(
																	"call-center"
																)}
															</a>
														</div>
													) : null}
												</div>
											</div>
										</aside>
										<div
											className={`col-12 ${
												market.config.home !== 5
													? `col-md-9`
													: ``
											}`}
										>
											<div className="tab-pane">
												<div
													className={`${
														activePanel === 1
															? "active"
															: "d-none"
													}`}
												>
													<DashboardTwo />
													{settings.phoneNumbers ? (
														<div className="info_support only-mobile">
															{/* <span>{t("have-questions")}</span> */}
															<a
																href={`tel:${settings.phoneNumbers[0]}`}
															>
																<i className="icon icon-headphones"></i>
																{t(
																	"call-center"
																)}
															</a>
														</div>
													) : null}
												</div>
												<div
													className={`${
														activePanel === 2
															? "active"
															: "d-none"
													}`}
												>
													<UserCards
														token={token}
														user={user}
													/>
												</div>

												<div
													className={`${
														activePanel === 3
															? "active"
															: "d-none"
													}`}
												>
													<div className="purchases">
														<Purchases
															token={token}
														/>
													</div>
												</div>

												{user.personType === "legal" ? (
													<div
														className={`${
															activePanel === 4
																? "active"
																: "d-none"
														}`}
													>
														<Company user={user} />
													</div>
												) : null}

												<div
													className={`${
														activePanel === 5
															? "active"
															: "d-none"
													}`}
												>
													<Settings
														user={user}
														token={token}
													/>
												</div>

												<div
													className={`${
														activePanel === 6
															? "active"
															: "d-none"
													}`}
												>
													<Addresses
														user={user}
														token={token}
													/>
												</div>
											</div>
										</div>
									</div>
								</div>
							</ul>
						</div>
					</div>
				</div>
			</div>
		</>
	);
}

export default React.memo(
	connect(
		state => ({
			user: state.user.user,
			token: state.user.tokenData?.token
		}),
		{
			emptyCart,
			logoutUser
		}
	)(Profile)
);
