import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import Modal from "react-modal";
import { useDispatch, useSelector } from "react-redux";
import { setUserAddresses } from "../../../actions/user";
import { addNewShippingAddress } from "../../../api/user";
import YandexMap from "../YandexMap";
import LoadingOverlay from "../loaders/LoaderOverlay";

const customStyles = {
	content: {
		top: "50%",
		transform: "translateY(-50%)",
		maxWidth: "100%",
		padding: "20px",
	},
	overlay: {
		backgroundColor: "rgba(77,77,77,0.6)",
		zIndex: "10000",
	},
};

function AddAddressModal({ addressMap, setAddressMap }) {
	const dispatch = useDispatch();
	const [loading, setLoading] = useState(false);
	const { t } = useTranslation();

	const [center, setCenter] = useState({
		center: [41.3112092101035, 69.27965188818546],
		zoom: 16,
	});
	const { token } = useSelector((state) => state.user.tokenData);

	const [address, setAddress] = useState({});
	const [disable, setDisable] = useState(false);

	function addAddress() {
		setLoading(true);

		addNewShippingAddress(address, token).then((res) => {
			if (res) {
				dispatch(setUserAddresses(res));
				setLoading(false);
				setDisable(false);
				setAddressMap(false);
			}
		});
	}

	useEffect(() => {
		setDisable(address.name ? false : true);
	}, [address]);

	return (
		<Modal
			isOpen={addressMap}
			onRequestClose={() => setAddressMap(!addAddress)}
			style={customStyles}
			className="modal-dialog map-modal"
			id="map-modal"
		>
			<div className="modal-content">
				<div className="modal-body">
					<button
						type="button"
						className="close closeMap"
						data-dismiss="modal"
						aria-label="Close"
						onClick={() => setAddressMap(!addressMap)}
					>
						<span aria-hidden="true">
							<i className="icon-close"></i>
						</span>
					</button>
					<div className="new_address_add">
						<div className="location-wrapper">
							<img
								src="assets/images/markerSearch.svg"
								alt="location"
								width="25"
								height="25"
							/>
						</div>
						<div className={loading ? "d-block loading" : "d-none"}>
							<LoadingOverlay />
						</div>

						<YandexMap
							center={center}
							setCenter={setCenter}
							onChange={(address) => {
								setAddress(address);
							}}
							height="100%"
						/>
						<p className="current_address">{address?.name}</p>

						<div className="add_address_btn text-center my-3">
							<button
								disabled={disable}
								className="btn btn-primary"
								onClick={() => {
									addAddress();
								}}
							>
								{t("add")}
							</button>
						</div>
					</div>
				</div>
			</div>
		</Modal>
	);
}

export default AddAddressModal;
