import React, { useEffect } from "react";
import Modal from "react-modal";
import { useTranslation } from "react-i18next";
import { formatterCurrency } from "../../../utils/string";
import SellRecordTwo from "../../../views/profile/dashboard/SellRecordTwo";
import { useSelector } from "react-redux";
import market from "../../../config/market";

const customStyles = {
	content: {
		maxWidth: "80%",
		top: "50%",
		transform: "translateY(-50%)",
		padding: "20px"
	},
	overlay: {
		backgroundColor: "rgba(77,77,77,0.6)",
		zIndex: "10000"
	}
};

export default function({ modal, setModal }) {
	const sell = modal.sell;

	if (!modal.open) {
		return "";
	}

	const { t } = useTranslation();

	let timer;
	const paidPaymentMethod = sell?.cashdesk?.paymentMethods.find(
		p => p.id === sell.payments[0].paymentMethod
	);
	const { shippingServices } = useSelector(state => state.marketData);

	const shippingService = shippingServices.find(
		s => s.id === sell.shippingService?.id
	);
	const { location } = useSelector(state => state.user);
	function closeModal() {
		document
			.getElementById("order-details-two")
			.classList.remove("ReactModal__Content--after-open");

		timer = setTimeout(() => {
			setModal({ open: false, product: {} });
		}, 200);
	}
	useEffect(() => {
		return () => {
			if (timer) clearTimeout(timer);
		};
	});

	return (
		<Modal
			isOpen={modal.open}
			className="modal-dialog"
			onRequestClose={closeModal}
			style={customStyles}
			id="order-details-two"
		>
			{modal.open && (
				<div>
					<div className="modal-content">
						<div className="orders_header">
							<p
								style={{ cursor: "pointer" }}
								className="exit-product-page"
								onClick={() => closeModal()}
							>
								<img
									src={market.config.icons.backArrow}
									alt="close"
								/>
							</p>
							<h3>{t("order-details")}</h3>
						</div>
						<div className="order-content">
							<div className="delivery-info">
								<p className="location">
									<img
										src={market.config.icons.location}
										alt="location"
									/>

									{location.address.name}
								</p>
								<p className="delivery-type">
									<img
										src={market.config.icons.car}
										alt="location"
									/>
									<button className="btn delyvery-opener">
										{t("delivery_type")}
										<span>
											{sell.shippingService?.name}, ~17
											
										</span>
									</button>
								</p>
								<p className="comment">
									<img
										src={market.config.icons.comment}
										alt="comment"
									/>
									<button className="btn comment-opener">
										{t("commnet")}
									</button>
								</p>
							</div>
							<div className="pay-info">
								<h3>{t("paying")}</h3>
								<p className="paying">
									<img
										src={market.config.icons.wallet}
										alt="wallet"
									/>
									<button className="btn pay-opener">
										{t("payment_method")}
										<span>{paidPaymentMethod?.name}</span>
									</button>
								</p>
							</div>

							<div className="order">
								<h3>{t("ordered")}</h3>
								<div className="orders">
									<div className="widget-body">
										<div className="sell_group">
											<div className="info_sell">
												<p className="sell-id">
													{t("order")} #{sell.id}
												</p>
												<p className="address">
													<img
														src={
															market.config.icons
																.location
														}
														alt="location"
													/>
													{location.address.name}
												</p>
											</div>
											<div className="sell_items">
												{sell.refundRecords.length > 0
													? sell.refundRecords.map(
															(
																sellRecord,
																index
															) => {
																return (
																	<SellRecordTwo
																		key={
																			index
																		}
																		refundRecord={
																			sellRecord
																		}
																		sellRecord={
																			sellRecord
																		}
																	/>
																);
															}
													  )
													: sell.sellRecords
															.filter(
																record =>
																	!record.deleted
															)
															.map(
																(
																	sellRecord,
																	index
																) => {
																	return (
																		<SellRecordTwo
																			key={
																				index
																			}
																			sellRecord={
																				sellRecord
																			}
																		/>
																	);
																}
															)}
											</div>
										</div>
									</div>
								</div>
							</div>

							<div className="info_sell_price">
								<div>
									<p>{t("products_two")}</p>
									{sell && sell.sellTotal ? (
										<p>
											{formatterCurrency(
												sell.sellTotal[
													sell.sellTotal.first
												]
											)}
										</p>
									) : null}
								</div>
								<div>
									<p>{t("delivery")}</p>
									{shippingService ? (
										<p>
											{formatterCurrency(
												shippingService.price[
													shippingService.price.first
												]
											)}
										</p>
									) : null}
								</div>
								{sell && sell.discount > 0 ? (
									<div>
										<p>{t("discount")}</p>
										<p>
											{formatterCurrency(
												sell.discount[
													sell.discount.first
												]
											)}
										</p>
									</div>
								) : null}
								{sell && sell.total ? (
									<div className="total">
										<b>
											<p>{t("total")}</p>
											<p>
												{formatterCurrency(
													sell.total[sell.total.first]
												)}
											</p>
										</b>
									</div>
								) : null}
							</div>
						</div>
					</div>
				</div>
			)}
		</Modal>
	);
}
