import React, { Fragment, useState } from "react";
import { updateUser } from "../../../api/user";
import LegalForm from "./LegalForm";
import { setUserData } from "../../../actions/user";
import { useDispatch, useSelector } from "react-redux";
import Loader from "../../../components/features/loaders/loader";
import { useTranslation } from "react-i18next";

export default function Company({ user }) {
	const { token } = useSelector(state => state.user.tokenData);
	const dispatch = useDispatch();
	const { t } = useTranslation();
	const [loading, setLoading] = useState(false);

	const handleSubmit = async values => {
		console.log(values);
		setLoading(true);
		const res = await updateUser({
			profile: {
				firstName: user.profile.firstName,
				// lastName: user.profile.lastName,
				gender: user.profile.gender,
				phoneNumber: user.profile.phoneNumbers[0]
			},
			personType: "legal",
			firmProfiles: values.firmProfile
		});
		if (res) {
			setLoading(false);
			dispatch(setUserData(res, token));
			window.location.reload();
		}
	};
	if (loading) {
		return <Loader />;
	}

	return (
		<Fragment>
			{user.firmProfiles?.length > 0 ? (
				<Fragment>
					<h1 className="title text-center">{t("my-companies")}</h1>
					<ul className="row mt-5 company">
						{user.firmProfiles.map((firm, index) => (
							<li
								key={index}
								className="col-lg-3 col-11 mx-auto mb-2 border"
							>
								<ul>
									<li className="px-4 py-2 mx-auto mb-1">
										<span>{t("firm-name")}</span>
										<p className="m-0">{firm.firmName}</p>
									</li>
									<li className="px-4 py-2 mx-auto mb-1">
										<span>{t("legal-name")}</span>
										<p className="m-0">{firm.legalName}</p>
									</li>
									<li className="px-4 py-2 mx-auto mb-1">
										<span>{t("inn")}</span>
										<p className="m-0">{firm.inn}</p>
									</li>
									<li className="px-4 py-2 mx-auto mb-1">
										<span>{t("mfo")}</span>
										<p className="m-0">{firm.mfo}</p>
									</li>
									<li className="px-4 py-2 mx-auto mb-1">
										<span>{t("okonx")}</span>
										<p className="m-0">{firm.okonh}</p>
									</li>
									<li className="px-4 py-2 mx-auto mb-1">
										<span>{t("firm-account")}</span>
										<p className="m-0">{firm.account}</p>
									</li>
									<li className="px-4 py-2 mx-auto mb-1">
										<span>{t("bank")}</span>
										<p className="m-0">{firm.bank}</p>
									</li>
									<li className="px-4 py-2 mx-auto mb-1">
										<span>{t("main-address")}</span>
										<p className="m-0">
											{firm.mainAddress}
										</p>
									</li>
									<li className="px-4 py-2 mx-auto mb-1">
										<span>{t("legal-address")}</span>
										<p className="m-0">
											{firm.legalAddress}
										</p>
									</li>
								</ul>
							</li>
						))}
					</ul>
				</Fragment>
			) : null}
			<LegalForm
				onSubmit={handleSubmit}
				firmProfiles={user.firmProfiles}
			/>
		</Fragment>
	);
}
