import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import YandexMap from "../../../components/features/YandexMap";
import LoadingOverlay from "../../../components/features/loaders/LoaderOverlay";
import { addNewShippingAddress } from "../../../api/user";
import { useDispatch, useSelector } from "react-redux";
import { setUserAddresses } from "../../../actions/user";
import AddressList from "./AddressList";
import market from "../../../config/market";
import AddAddressModal from "../../../components/features/modal/AddAddressModal";

const Addresses = ({ token }) => {
	const dispatch = useDispatch();
	const [loading, setLoading] = useState(false);
	const { t } = useTranslation();
	const [addressMap, setAddressMap] = useState(false);
	const [center, setCenter] = useState({
		center: [41.3112092101035, 69.27965188818546],
		zoom: 16
	});
	const shippingAddress = useSelector(state =>
		state.user.user?.addresses?.filter(a => a.type === "shipping")
	);
	const [address, setAddress] = useState({});
	const [disable, setDisable] = useState(false);

	function addAddress() {
		setLoading(true);
		addNewShippingAddress(address, token).then(res => {
			if (res) {
				dispatch(setUserAddresses(res));
				setLoading(false);
				setDisable(false);
			}
		});
	}

	useEffect(() => {
		setDisable(address.name ? false : true);
	}, [address]);

	return (
		<div className="user_addresses_page">
			{market.config.home === 8 ? (
				<div className="title-add-address w-100 d-flex align-items-center justify-content-between">
					<h3>{t(" ")}</h3>

					<button
						className="btn d-flex align-items-center"
						onClick={() => setAddressMap(!addressMap)}
					>
						<svg
							width="21"
							height="20"
							viewBox="0 0 21 20"
							fill="none"
							className="mr-2"
							xmlns="http://www.w3.org/2000/svg"
						>
							<path
								d="M11.4779 5.59408C11.4779 5.17986 11.1421 4.84408 10.7279 4.84408C10.3137 4.84408 9.97792 5.17986 9.97792 5.59408L11.4779 5.59408ZM9.97792 14.8507C9.97792 15.265 10.3137 15.6007 10.7279 15.6007C11.1421 15.6007 11.4779 15.265 11.4779 14.8507L9.97792 14.8507ZM6.09959 9.47241C5.68537 9.47241 5.34959 9.8082 5.34959 10.2224C5.34959 10.6366 5.68537 10.9724 6.09959 10.9724V9.47241ZM15.3563 10.9724C15.7705 10.9724 16.1063 10.6366 16.1063 10.2224C16.1063 9.8082 15.7705 9.47241 15.3563 9.47241V10.9724ZM9.97792 5.59408L9.97792 10.2224H11.4779L11.4779 5.59408L9.97792 5.59408ZM9.97792 10.2224L9.97792 14.8507L11.4779 14.8507L11.4779 10.2224H9.97792ZM6.09959 10.9724L10.7279 10.9724V9.47241L6.09959 9.47241V10.9724ZM10.7279 10.9724L15.3563 10.9724V9.47241L10.7279 9.47241V10.9724ZM16.5616 4.38878C19.7834 7.61061 19.7834 12.8342 16.5616 16.056L17.6222 17.1167C21.4298 13.3091 21.4298 7.13573 17.6222 3.32812L16.5616 4.38878ZM16.5616 16.056C13.3397 19.2779 8.11612 19.2779 4.89429 16.056L3.83363 17.1167C7.64124 20.9243 13.8146 20.9243 17.6222 17.1167L16.5616 16.056ZM4.89429 16.056C1.67247 12.8342 1.67247 7.61061 4.89429 4.38878L3.83363 3.32812C0.026019 7.13573 0.026019 13.3091 3.83363 17.1167L4.89429 16.056ZM4.89429 4.38878C8.11612 1.16696 13.3397 1.16696 16.5616 4.38878L17.6222 3.32812C13.8146 -0.479491 7.64124 -0.479491 3.83363 3.32812L4.89429 4.38878Z"
								fill="#1890FF"
							/>
						</svg>
						<span>{t(" ")}</span>
					</button>
				</div>
			) : null}
			<p className="default-add">{t("default-addresses")}</p>
			{shippingAddress.length > 0 ? (
				<AddressList shippingAddress={shippingAddress} />
			) : null}
			{market.config.home === 8 ? null : (
				<div className="new_address_add">
					<div className={loading ? "d-block loading" : "d-none"}>
						<LoadingOverlay />
					</div>
					<div className="location-wrapper">
						<img
							src="assets/images/pin.png"
							alt="location"
							width="22"
							height="22"
						/>
					</div>
					<YandexMap
						center={center}
						setCenter={setCenter}
						onChange={address => {
							setAddress(address);
						}}
						height="300px"
					/>

					<p className="current_address">
						<b>{address?.name}</b>
					</p>

					<div className="add_address_btn text-center my-3">
						<button
							disabled={disable}
							className="btn btn-primary"
							onClick={() => {
								addAddress();
							}}
						>
							{t("add")}
						</button>
					</div>
				</div>
			)}

			<AddAddressModal
				addressMap={addressMap}
				setAddressMap={setAddressMap}
			/>
		</div>
	);
};

export default Addresses;
