import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { deleteUserCard, getUserCard } from "../../../api/user";
import AddUserCard from "../../../components/features/modal/UserCard/AddUserCardModal";
import timee from "../../../assets/images/Time.png";
import AcseptModal from "../../../components/features/modal/AcseptModal";
import { openModal } from "../../../actions/modal";
import market from "../../../config/market";

export default function UserCards() {
	const { t } = useTranslation();
	const [userCards, setUserCards] = useState([]);
	const { token } = useSelector((state) => state.user.tokenData);
	const [acseptModal, setAcseptModal] = useState({ item: null, open: false });
	const dispatch = useDispatch();
	const [userCardTab, setUserCardTab] = useState(false);

	function openPayment() {
		dispatch(openModal("usercard"));
		setUserCardTab(true);
	}
	useEffect(() => {
		getUserCard(token).then((res) => {
			setUserCards(res);
		});
	}, []);

	function isClicked(item) {
		deleteUserCard(item.id, token);
		setUserCards(userCards.filter((card) => card.id !== item.id));
	}

	return (
		<>
			<div className="user-cards-list">
				<div className="title-add-card w-100 d-flex align-items-center justify-content-between">
					<h3>{t("my_cards")}</h3>
					<button
						className="btn d-flex align-items-center"
						onClick={openPayment}
					>
						<svg
							width="21"
							height="20"
							viewBox="0 0 21 20"
							fill="none"
							className="mr-2"
							xmlns="http://www.w3.org/2000/svg"
						>
							<path
								d="M11.4779 5.59408C11.4779 5.17986 11.1421 4.84408 10.7279 4.84408C10.3137 4.84408 9.97792 5.17986 9.97792 5.59408L11.4779 5.59408ZM9.97792 14.8507C9.97792 15.265 10.3137 15.6007 10.7279 15.6007C11.1421 15.6007 11.4779 15.265 11.4779 14.8507L9.97792 14.8507ZM6.09959 9.47241C5.68537 9.47241 5.34959 9.8082 5.34959 10.2224C5.34959 10.6366 5.68537 10.9724 6.09959 10.9724V9.47241ZM15.3563 10.9724C15.7705 10.9724 16.1063 10.6366 16.1063 10.2224C16.1063 9.8082 15.7705 9.47241 15.3563 9.47241V10.9724ZM9.97792 5.59408L9.97792 10.2224H11.4779L11.4779 5.59408L9.97792 5.59408ZM9.97792 10.2224L9.97792 14.8507L11.4779 14.8507L11.4779 10.2224H9.97792ZM6.09959 10.9724L10.7279 10.9724V9.47241L6.09959 9.47241V10.9724ZM10.7279 10.9724L15.3563 10.9724V9.47241L10.7279 9.47241V10.9724ZM16.5616 4.38878C19.7834 7.61061 19.7834 12.8342 16.5616 16.056L17.6222 17.1167C21.4298 13.3091 21.4298 7.13573 17.6222 3.32812L16.5616 4.38878ZM16.5616 16.056C13.3397 19.2779 8.11612 19.2779 4.89429 16.056L3.83363 17.1167C7.64124 20.9243 13.8146 20.9243 17.6222 17.1167L16.5616 16.056ZM4.89429 16.056C1.67247 12.8342 1.67247 7.61061 4.89429 4.38878L3.83363 3.32812C0.026019 7.13573 0.026019 13.3091 3.83363 17.1167L4.89429 16.056ZM4.89429 4.38878C8.11612 1.16696 13.3397 1.16696 16.5616 4.38878L17.6222 3.32812C13.8146 -0.479491 7.64124 -0.479491 3.83363 3.32812L4.89429 4.38878Z"
								fill="#1890FF"
							/>
						</svg>
						<span>{t("add-card")}</span>
					</button>
				</div>
				{userCards?.length > 0 ? (
					userCards.map((item, index) => (
						<div className="row" key={index}>
							<div className="user-card d-flex col-12 justify-content-between align-items-center">
								<div className="card-number-date d-flex align-items-center">
									<h3 className="card-number">
										{item.cardNumber}
									</h3>
									<h3 className="card-date d-flex align-items-center">
										<img
											src={timee}
											alt="time"
											className="mr-2"
										/>
										{item.cardExpiry.substring(0, 2) +
											"/" +
											item.cardExpiry.slice(2)}
									</h3>
								</div>
								<div className="active-delete d-flex align-items-center">
									<div className="is-active d-flex align-items-center">
										<div
											className={
												item.active
													? `active`
													: `not-active`
											}
										></div>
										<h3>
											{item.active
												? t("active")
												: t("not_active")}
										</h3>
									</div>
									<button
										className="delete-button d-flex align-items-center justify-content-center"
										onClick={() =>
											setAcseptModal({
												item: item,
												open: true,
											})
										}
									>
										<img
											src={market.config.icons.deleteIcon}
											alt="delete"
										/>
									</button>
								</div>
							</div>
						</div>
					))
				) : (
					<div className="no-data">
						<h3>{t("no-user-cards")}</h3>
						<img
							src="/assets/images/wooz/credit-card.png"
							alt="no-data"
						/>
					</div>
				)}
				<AddUserCard
					setUserCards={setUserCards}
					userCardTab={userCardTab}
					setUserCardTab={setUserCardTab}
				/>
			</div>
			{acseptModal.open ? (
				<AcseptModal
					title="are_you_sure"
					isClicked={isClicked}
					acseptModal={acseptModal}
					setAcseptModal={setAcseptModal}
				/>
			) : null}
		</>
	);
}
